import React, { useMemo } from 'react'
import ReactSpeedometer from 'react-d3-speedometer'

interface GaugeProps {
    value: number
    topLimitWarn?: number
    topLimitAlarm?: number
    bottomLimitWarn?: number
    bottomLimitAlarm?: number
    minValue?: number
    maxValue?: number
    needleColor?: string
    segmentColors?: string[]
    segments?: number
    width?: number | string
    height?: number | string
    unit?: string
}

const Gauge: React.FC<GaugeProps> = ({
    value,
    topLimitWarn,
    topLimitAlarm,
    bottomLimitWarn,
    bottomLimitAlarm,
    minValue,
    maxValue,
    needleColor = '#2e2e2e',
    segmentColors = ['#118a7e'],
    segments = 3,
    width = '100%',
    height = '100%',
    unit = '',
}) => {

    const computedLimits = useMemo(() => {
        const values = []
        if (topLimitWarn != undefined) values.push(topLimitWarn)
        if (topLimitAlarm != undefined) values.push(topLimitAlarm)
        if (bottomLimitWarn != undefined) values.push(bottomLimitWarn)
        if (bottomLimitAlarm != undefined) values.push(bottomLimitAlarm)

        const rawMax = values.length == 0 ? 100 : Math.max(...values)
        const rawMin = values.length == 0 ? 0 : Math.min(...values)

        const delta = rawMax * 0.25

        const autoMax = rawMax + delta
        const autoMin = rawMin - delta

        return {
            min: minValue != undefined ? minValue : autoMin,
            max: maxValue != undefined ? maxValue : autoMax,
        }
    }, [topLimitWarn, topLimitAlarm, bottomLimitWarn, bottomLimitAlarm, minValue, maxValue])

    const { computedStops, computedSegmentColors } = useMemo(() => {
        const stops: number[] = []
        const colors: string[] = []

        if (
            bottomLimitAlarm != undefined &&
            bottomLimitWarn != undefined &&
            topLimitWarn != undefined &&
            topLimitAlarm != undefined
        ) {
            stops.push(
                computedLimits.min,
                bottomLimitAlarm,
                bottomLimitWarn,
                topLimitWarn,
                topLimitAlarm,
                computedLimits.max
            )

            colors.push(
                '#9f1e49',
                '#ca9257',
                '#118a7e',
                '#ca9257',
                '#9f1e49'
            )

            const sortedStops = [...stops].sort((a, b) => a - b)
            return {
                computedStops: sortedStops,
                computedSegmentColors: colors,
            }
        }

        return {
            computedStops: undefined,
            computedSegmentColors: segmentColors,
        }
    }, [
        bottomLimitAlarm,
        bottomLimitWarn,
        topLimitWarn,
        topLimitAlarm,
        computedLimits.min,
        computedLimits.max,
        segmentColors,
    ])

    const gaugeKey = useMemo(() => {
        return [
            computedStops?.join(','),
            computedLimits.min,
            computedLimits.max,
            computedSegmentColors.join(','),
            segments,
        ].join('|')
    }, [computedStops, computedLimits.min, computedLimits.max, computedSegmentColors, segments])

    const memoizedGauge = useMemo(() => (
        <ReactSpeedometer
            key={gaugeKey}
            value={value > computedLimits.max ? computedLimits.max : value < computedLimits.min ? computedLimits.min : value}
            minValue={computedLimits.min}
            maxValue={computedLimits.max}
            needleColor={needleColor}
            segmentColors={computedSegmentColors}
            customSegmentStops={computedStops}
            segments={computedStops ? computedStops.length - 1 : segments}
            textColor="white"
            width={typeof width === 'string' ? 250 : width}
            height={typeof height === 'string' ? 200 : height}
            currentValueText={`${value}  ${unit}`}
        />
    ), [
        value,
        computedLimits.min,
        computedLimits.max,
        needleColor,
        computedSegmentColors,
        computedStops?.join(','),
        segments,
        width,
        height,
        gaugeKey,
        unit
    ])

    return (
        <div
            style={{
                display: 'flex',
                justifyContent: 'center',
                color: 'red',
                height: '100%',
                width: '100%',
            }}
        >
            {memoizedGauge}
        </div>
    )
}

export default Gauge
