import { BASE_URL } from "../constants"
import { BusinessUnitConfigElectric, ElectricData, ElectricEntity, ElectricInsertEntity, ElectricUpdateEntity, DashboardElectricLevel1, DashboardElectricLevel2, DashboardElectricLevel3, FormBusinessUnitElectric, FormLinkupElectric, LinkUpConfigElectric, } from "../entities/ElectricEntity"
import FetchUtils from "../utils/fetch"

class ElectricService {
    static async GetAllElectric(link_up_id?: string): Promise<ElectricEntity[]> {

        let uri = `${BASE_URL}/api/extension/electric/v1`
        if (link_up_id != null) {
            uri += `?link_up_id=${link_up_id}`
        }

        const resp = await FetchUtils.fetchAuth(uri)
        if (resp.status !== 200) {
            throw new Error(resp.message)
        }

        const data: ElectricEntity[] = Array.isArray(resp.data) ? resp.data : []
        for (let i = 0; i < data.length; i++) {
            if (typeof data[i].electric_id != "number") { throw new Error("invalid electric_id value") }
            if (typeof data[i].link_up_id != "number") { throw new Error("invalid link_up_id value") }
            if (typeof data[i].link_up != "string") { throw new Error("invalid link_up value") }
            if (typeof data[i].parameter_name != "string") { throw new Error("invalid parameter_name value") }
            if (typeof data[i].electric_tipe != "string") { throw new Error("invalid electric_tipe value") }
            if (data[i].electric_warn != undefined && typeof data[i].electric_warn != "number") { throw new Error("invalid electric_warn value") }
            if (data[i].electric_alarm != undefined && typeof data[i].electric_alarm != "number") { throw new Error("invalid electric_alarm value") }
            if (data[i].electric_bottom_warn != undefined && typeof data[i].electric_bottom_warn != "number") { throw new Error("invalid electric_bottom_warn value") }
            if (data[i].electric_bottom_alarm != undefined && typeof data[i].electric_bottom_alarm != "number") { throw new Error("invalid electric_bottom_alarm value") }
            if (typeof data[i].electric_display_type != "string") { throw new Error("invalid electric_display_type value") }
            if (typeof data[i].electric_unit != "string") { throw new Error("invalid electric_unit value") }
        }

        return data
    }

    static async AddElectric(data: ElectricInsertEntity): Promise<{ message: string }> {
        const resp = await FetchUtils.fetchAuth(`${BASE_URL}/api/extension/electric/v1`,
            {
                method: 'POST',
                body: JSON.stringify({ ...data }),
                headers: { "Content-Type": "application/json" }
            }
        )

        if (resp.status !== 200) {
            throw new Error(resp.message)
        }

        return resp
    }

    static async UpdateElectric(electric_id: number, fields: ElectricUpdateEntity): Promise<{ message: string }> {
        const resp = await FetchUtils.fetchAuth(`${BASE_URL}/api/extension/electric/v1`,
            {
                method: 'PATCH',
                body: JSON.stringify({ electric_id, fields }),
                headers: { "Content-Type": "application/json" }
            }
        )

        if (resp.status !== 200) {
            throw new Error(resp.message)
        }

        return resp
    }

    static async DeleteElectric(electric_id: number): Promise<{ message: string }> {
        const resp = await FetchUtils.fetchAuth(`${BASE_URL}/api/extension/electric/v1`,
            {
                method: 'DELETE',
                body: JSON.stringify({ electric_id }),
                headers: { "Content-Type": "application/json" }
            }
        )

        if (resp.status !== 200) {
            throw new Error(resp.message)
        }

        return resp
    }

    static async GetLinkUpConfig(link_up_id?: number): Promise<LinkUpConfigElectric[]> {
        let uri = `${BASE_URL}/api/extension/electric/v1/linkup`
        if (link_up_id != null) {
            uri += `?link_up_id=${link_up_id}`
        }

        const resp = await FetchUtils.fetchAuth(uri)
        if (resp.status !== 200) {
            throw new Error(resp.message)
        }

        if (!Array.isArray(resp.data)) {
            throw new Error("invalid link up config response")
        }
        for (let i = 0; i < resp.data.length; i++) {
            const row = resp.data[i]
            if (typeof row.link_up_id != "number") { throw new Error("invalid link_up_id value") }
            if (typeof (row.electric_warn ?? 0) != "number") { throw new Error("invalid electric_warn value") }
            if (typeof (row.electric_alarm ?? 0) != "number") { throw new Error("invalid electric_alarm value") }
        }

        return resp.data
    }

    static async UpsertLinkupConfig(link_up_id: number, fields: FormLinkupElectric): Promise<{ message: string }> {
        const resp = await FetchUtils.fetchAuth(`${BASE_URL}/api/extension/electric/v1/linkup`, {
            method: 'PATCH',
            body: JSON.stringify({ link_up_id, fields }),
            headers: { "Content-Type": "application/json" }
        })

        if (resp.status !== 200) {
            throw new Error(resp.message)
        }

        return resp
    }

    static async GetBusinessUnitConfig(business_unit_id?: number): Promise<BusinessUnitConfigElectric[]> {
        let uri = `${BASE_URL}/api/extension/electric/v1/business`
        if (business_unit_id != null) {
            uri += `?business_unit_id=${business_unit_id}`
        }

        const resp = await FetchUtils.fetchAuth(uri)
        if (resp.status !== 200) {
            throw new Error(resp.message)
        }

        if (!Array.isArray(resp.data)) {
            throw new Error("invalid link up config response")
        }
        for (let i = 0; i < resp.data.length; i++) {
            const row = resp.data[i]
            if (typeof row.business_unit_id != "number") { throw new Error("invalid business_unit_id value") }
            if (typeof (row.electric_warn ?? 0) != "number") { throw new Error("invalid electric_warn value") }
            if (typeof (row.electric_alarm ?? 0) != "number") { throw new Error("invalid electric_alarm value") }
        }

        return resp.data
    }

    static async UpsertBusinessUnitConfig(business_unit_id: number, fields: FormBusinessUnitElectric): Promise<{ message: string }> {
        const resp = await FetchUtils.fetchAuth(`${BASE_URL}/api/extension/electric/v1/business`, {
            method: 'PATCH',
            body: JSON.stringify({ business_unit_id, fields }),
            headers: { "Content-Type": "application/json" }
        })

        if (resp.status !== 200) {
            throw new Error(resp.message)
        }

        return resp
    }
}

