import { useContext, useEffect, useState } from "react"
import AppContext from "../../../context"
import { useNavigate, useLocation, useParams } from "react-router-dom"
import { AlertEntity } from "../../../layouts/Alert/AlertEntity"
import styles from './DashboardLevel3.module.css'
import { formatDate } from "date-fns"
import sensorImage from "../../../assets/images/electric_image.png"
import { ElectricService, DashboardElectricService } from "../../../services/electric"
import { ElectricEntity, DashboardElectricLevel3 } from "../../../entities/ElectricEntity"
import Gauge from "../../../components/Gauge/Gauge"
import { FaRegCircleCheck } from "react-icons/fa6"
import { MdOutlineCancel } from "react-icons/md"
import noDataImg from '../../../assets/images/no_data_image.png'

interface FilterType {
    start_date: Date
    end_date: Date
}

interface DashboardLevel3ElectricPageProps {
    realtime: boolean
    refresh: number
    filterDate: FilterType
    onFilterDateChange: (newFilter: FilterType) => void
}

const DashboardLevel3ElectricPage: React.FC<DashboardLevel3ElectricPageProps> = ({ realtime, refresh, filterDate, onFilterDateChange }) => {
    // ========================================= GLOBAL CONTEXT =========================================  //
    const context = useContext(AppContext)
    const contextVersion = context.version
    const contextAccessToken = context.contextAccessToken
    const contextShowAlertFunc = context.contextShowAlertFunc
    const contextShowLoadingFunc = context.contextShowLoadingFunc
    const stateShowLoading = context.stateShowLoading
    const navigate = useNavigate()
    const location = useLocation()
    // ========================================= GLOBAL CONTEXT =========================================  //


    // ========================================= STATE VIEW =========================================  //
    const { linkUpId } = useParams()
    const { link_up } = location.state || {}

    const [machineElectricData, setMachineElectricData] = useState<{ [type: string]: ElectricEntity[] }>({})
    const [tableDataElectric, setTableDataElectric] = useState<{ [id: string]: DashboardElectricLevel3 | undefined }>({})

    interface DashboardLevel2State {
        link_up_id: number
        link_up: string

        electric_green: number
        electric_yellow: number
        electric_red: number

        machine_ool: number[]
    }

    const [level2Data, setLevel2Data] = useState<{ [type: string]: DashboardLevel2State }>({})
    // ========================================= STATE VIEW =========================================  //


    // ========================================= FUNCTIONS =========================================  //
    const generateDataMachineElectric = async () => {
        if (Object.keys(machineElectricData).length == 0) { contextShowLoadingFunc(true) }
        try {
            const resp = await ElectricService.GetAllElectric(linkUpId)

            const data: { [type: string]: ElectricEntity[] } = {}
            for (let i = 0; i < resp.length; i++) {
                if (data[resp[i].electric_tipe] == undefined) {
                    data[resp[i].electric_tipe] = []
                }
                data[resp[i].electric_tipe].push(resp[i])
            }

            setMachineElectricData(data)
        } catch (error: any) {
            contextShowAlertFunc(new AlertEntity({ message: error instanceof Error ? error.message : error.toString(), level: 3, }))
        } finally {
            if (Object.keys(machineElectricData).length == 0) { contextShowLoadingFunc(false) }
        }
    }

    const generateDataElectric = async () => {
        try {
            if (linkUpId) {
                let resp: DashboardElectricLevel3[] = []
                if (realtime) {
                    resp = await DashboardElectricService.DashboardLevel3Realtime(Number(linkUpId))

                    onFilterDateChange({ start_date: new Date(), end_date: new Date() })
                    localStorage.removeItem("cache")
                } else {
                    const start_date = formatDate(filterDate.start_date, 'yyyy-MM-dd')
                    const end_date = formatDate(filterDate.end_date, 'yyyy-MM-dd')

                    resp = await DashboardElectricService.DashboardLevel3(start_date, end_date, Number(linkUpId))
                    localStorage.setItem("cache", JSON.stringify({ ...filterDate, realtime }))
                }

                const data: { [id: string]: DashboardElectricLevel3 } = {}
                const newLevel2ByType: { [type: string]: DashboardLevel2State } = {}
                for (let i = 0; i < resp.length; i++) {
                    const row = resp[i]
                    data[`${row.electric_id}`] = row

                    const machine = Object.values(machineElectricData).flat().find(m => m.electric_id === row.electric_id)
                    if (!machine) continue

                    const machineType = machine.electric_tipe

                    if (!newLevel2ByType[machineType]) {
                        newLevel2ByType[machineType] = {
                            link_up_id: link_up.link_up_id,
                            link_up: link_up.link_up,
                            electric_green: 0,
                            electric_yellow: 0,
                            electric_red: 0,
                            machine_ool: [],
                        }
                    }

                    newLevel2ByType[machineType].electric_green += row.electric_green
                    newLevel2ByType[machineType].electric_yellow += row.electric_yellow
                    newLevel2ByType[machineType].electric_red += row.electric_red

                    if (row.electric_red > 0) {
                        newLevel2ByType[machineType].machine_ool.push(machine.electric_id)
                    }
                }

                for (const type in newLevel2ByType) {
                    newLevel2ByType[type].machine_ool = Array.from(new Set(newLevel2ByType[type].machine_ool))
                }

                setLevel2Data(newLevel2ByType)
                setTableDataElectric(data)
            }
        } catch (error: any) {
            contextShowAlertFunc(new AlertEntity({ message: error instanceof Error ? error.message : error.toString(), level: 3 }))
        }
    }

    const generateData = async () => {
        if (realtime) {
            if (contextVersion.electric != undefined) { await generateDataElectric() }
        } else {
            resetAllData()
            contextShowLoadingFunc(true)
            // langsung tampilkan ketika udah ada yg selesai satu
            const promise = []
            if (contextVersion.electric != undefined) { promise.push(generateDataElectric()) }
            try { await Promise.any(promise) } catch { }
            contextShowLoadingFunc(false)
        }
    }

    const resetAllData = () => {
        setLevel2Data((prev) => {
            const reset: typeof prev = {}
            for (const type in prev) {
                reset[type] = {
                    ...prev[type],
                    electric_green: 0,
                    electric_yellow: 0,
                    electric_red: 0,
                    machine_ool: [],
                }
            }
            return reset
        })
        setTableDataElectric({})
    }

    useEffect(() => {
        if (Object.keys(machineElectricData).length > 0) {
            generateData()
            if (realtime) {
                let timeout: NodeJS.Timeout | null = null
                const refreshfunc = () => {
                    timeout = setTimeout(async () => { await generateData(); refreshfunc() }, 3000)
                }
                refreshfunc()
                return () => {
                    if (timeout != null) { clearTimeout(timeout) }
                }
            }
        }
    }, [contextAccessToken, realtime, linkUpId, machineElectricData, refresh])

    // generate once
    useEffect(() => {
        generateDataMachineElectric()
    }, [])
    // ========================================= FUNCTIONS =========================================  //

    if (Object.keys(machineElectricData).length === 0) {
        return (
            <div className={styles.noData}>
                <div className="flex flex-col items-center justify-center text-gray-400">
                    <img src={noDataImg} alt="no_data_img" width={140} height={140} />
                    <p className="mt-2 text-xl font-medium">No Data Available</p>
                </div>
            </div>
        )
    }

    return <div
        className={styles.cardWrapper}
        style={{
            gridTemplateColumns: Object.keys(machineElectricData).length === 1 ? '1fr' : 'repeat(2, 1fr)',
            gridAutoRows: Object.keys(machineElectricData).length <= 2 ? '1fr' : undefined
        }}
    >
        {Object.keys(machineElectricData).map((val) => {
            const row = machineElectricData[val]
            return < div className={styles.cardParent} key={val} >

                <div className={styles.cardParentHeader}>
                    <div className={styles.titleHeader}>
                        {val} Overall
                    </div>

                    <div className={styles.bodyHeader}>
                        <div className={styles.bodyHeaderLeft}>
                            <div className={styles.itemHeaderLeft}>
                                <div>Total Machine OOL</div>
                                <div className={styles.oolNumber}>{level2Data[val]?.machine_ool?.length ?? 0}</div>
                            </div>
                            <div className={styles.itemHeaderLeft}>
                                <div>Electric OK</div>
                                <div className={`${styles.oolNumber} ${styles.cenNumberNormal}`}>{level2Data[val]?.electric_green}</div>
                            </div>
                            <div className={styles.itemHeaderLeft}>
                                <div>Electric OOL</div>
                                <div className={`${styles.oolNumber} ${styles.cenNumberAlarm}`}>{level2Data[val]?.electric_red}</div>
                            </div>
                            {/* <div className={styles.itemHeaderLeft}>
                                <div>Electric Normal</div>
                                <div className={`${styles.oolNumber} ${styles.cenNumberNormal}`}>{level2Data[val]?.electric_green}</div>
                            </div>
                            <div className={styles.itemHeaderLeft}>
                                <div>Electric Warning</div>
                                <div className={`${styles.oolNumber} ${styles.cenNumberWarning}`}>{level2Data[val]?.electric_yellow}</div>
                            </div>
                            <div className={styles.itemHeaderLeft}>
                                <div>Electric Alarm</div>
                                <div className={`${styles.oolNumber} ${styles.cenNumberAlarm}`}>{level2Data[val]?.electric_red}</div>
                            </div> */}
                        </div>

                        <div className={styles.bodyHeaderRight}>
                            <div className={styles.imgWrapper}>
                                <img src={sensorImage} alt="electric image" className={styles.imgHeader} />
                            </div>
                        </div>
                    </div>
                </div>

                <div className={styles.cardParentBodyWrapper}>
                    <div className={styles.cardParentBody} style={{ gridTemplateColumns: Object.keys(machineElectricData).length > 1 ? "repeat(2, 1fr)" : "repeat(4, 1fr)" }}>
                        {row.map((m) => {
                            const data = tableDataElectric[`${m.electric_id}`]

                            return <div key={m.electric_id} className={styles.gridItemCenter}
                                onClick={() => navigate(`/dashboard/l4/${m.electric_id}`, {
                                    state: {
                                        dashboard_type: "electric",
                                        machine: m.parameter_name,
                                    },
                                })}>

                                {data && (
                                    <>
                                        <div className={styles.gridCenterHeader}>
                                            <h3>{data.parameter_name}</h3>
                                            <p>{realtime ? formatDate(data.last_electric_data.created_at, "HH:mm:ss") : formatDate(data.last_electric_data.created_at, "yyyy-MM-dd HH:mm:ss")}</p>
                                        </div>
                                        <div className={styles.gridCenterContent}>
                                            {m.electric_display_type == 'gauge'
                                                ? <>
                                                    <div className={styles.warningAlarm}>
                                                        <div className={styles.alarmColorBox}>{data.last_electric_data.alarm_limit_bottom ?? "-"}</div>
                                                        {data && data.last_electric_data.alarm_limit_bottom != data.last_electric_data.warning_limit_bottom && (
                                                            <div className={styles.warnColorBox}>{data.last_electric_data.warning_limit_bottom ?? "-"}</div>
                                                        )}
                                                        {data && data.last_electric_data.alarm_limit != data.last_electric_data.warning_limit && (
                                                            <div className={styles.warnColorBox}>{data.last_electric_data.warning_limit ?? "-"}</div>
                                                        )}
                                                        <div className={styles.alarmColorBox}>{data.last_electric_data.alarm_limit ?? "-"}</div>
                                                    </div>
                                                    <div className={styles.gaugeContent}>
                                                        <Gauge
                                                            value={data.last_electric_data.value}
                                                            topLimitWarn={data.last_electric_data.warning_limit}
                                                            topLimitAlarm={data.last_electric_data.alarm_limit}
                                                            bottomLimitWarn={data.last_electric_data.warning_limit_bottom}
                                                            bottomLimitAlarm={data.last_electric_data.alarm_limit_bottom}
                                                            height={220}
                                                            width={220}
                                                            unit={m.electric_unit}
                                                        />
                                                    </div>
                                                </>
                                                : <>
                                                    <div className={`${styles.booleanContent} ${data.last_electric_data.value == 1 ? '' : styles.notOk}`}>
                                                        <div className={styles.booleanLeft}>
                                                            <h2>Status</h2>
                                                            <h2>{data.last_electric_data.value == 1 ? 'OK' : 'Not OK'}</h2>
                                                        </div>
                                                        <div className={styles.booleanRight}>
                                                            <div className={styles.booleanStatus}>
                                                                {data.last_electric_data.value == 1 ? <FaRegCircleCheck size={25} /> : <MdOutlineCancel size={25} />}
                                                            </div>
                                                        </div>
                                                    </div>
                                                </>}
                                        </div>
                                    </>
                                )}
                            </div>
                        })}
                    </div>
                </div>

            </div>
        })}
    </div>
}

export default DashboardLevel3ElectricPage